@import url(https://fonts.googleapis.com/css?family=Poppins);
@import url(https://fonts.googleapis.com/css?family=Poppins);
body {
  margin: 0;
  padding: 0;
  font-family: Poppins, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  font-family: Poppins, sans-serif;
  display: block;
}

.DescriptionSection {
    background-image: url(/ilhamdcp-portfolio/static/media/IMG_6992.16055068.jpg);
    background-size: cover;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -2;
}

.DescriptionSection::after {
    background-color: rgb(60, 78, 95);
    content: "";
    display: block;
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    z-index: -1;
    opacity: 0.8;
}

.Description {
    margin: 20% auto;
    margin-left: 40%;
    width: 50%;
}

p, h2 {
    font-size: 18pt;
    padding: 10px;
    color: white;
}

.Name {
    font-size: 36pt;
}

.LanguageSection {
    z-index: -2;
}

.Container {
    width: 80%;
    display: inline;
}

.languages {
    padding: 10px;
    background: rgb(38, 156, 117);
    border-radius: 10px;
    color: white;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}
